import React, { useState, useEffect, useRef } from 'react';
import uuidv4 from 'uuid/v4';
import data from '../../lib/data';
import dragAndDrop from '../../lib/dragAndDrop';

const JobContext = React.createContext();

const JobContextProvider = ({ children }) => {
  const [state, setState] = useState();

  const isInitialMount = useRef(true);

  useEffect(() => {
    if (isInitialMount.current) {
      isInitialMount.current = false;
      const initialState = JSON.parse(localStorage.getItem('data')) || data;
      setState(initialState);
    } else {
      localStorage.setItem('data', JSON.stringify(state));
    }
  });

  const onDragEnd = result => dragAndDrop(result, state, setState);

  const modalTrigger = data => {
    setState({
      ...state,
      isModalOpen: !state.isModalOpen,
      modalData: {
        ...state.modalData,
        ...data
      }
    });
  };

  const editModal = data => {
    console.log(data);
  };

  const modalSubmit = modalValues => {
    const { company, position } = modalValues;

    const uniqId = uuidv4();

    const tempJob = {
      id: uniqId,
      company,
      position,
      date: Date.now()
    };
  };

  const newJob = []

  return (
    <JobContext.Provider
      value={{ ...state, onDragEnd, modalTrigger, modalSubmit, editModal }}
    >
      {children}
    </JobContext.Provider>
  );
};

export default JobContextProvider;
export { JobContext };
